<template>
  <div>
    <h3>Rename project</h3>
    <br />
    <v-form @submit.prevent="renameSketch()">
      <v-text-field
        outlined
        dense
        autofocus
        v-model="newSketchName"
        @focus="$event.target.select()"
        clearable
        :rules="sketchNameRules"
      >
      </v-text-field>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn text @click="closeDialog()"> Cancel </v-btn>
        <v-btn :disabled="!newSketchName || newSketchName.length > 255" text color="primary" @click="renameSketch()">
          Save
        </v-btn>
      </v-card-actions>
    </v-form>
  </div>
</template>

<script>
export default {
  props: ['sketchId'],
  data() {
    return {
      newSketchName: '',
      sketchNameRules: [
        (v) => !!v || 'Sketch name is required.',
        (v) => (v && v.length <= 255) || 'Sketch name is too long.',
      ],
    }
  },
  computed: {
    sketch() {
      return this.$store.state.sketch
    },
  },
  methods: {
    renameSketch() {
      // Update virtual sketch name in localStorage
      localStorage.setItem('sketchName', this.newSketchName)
      
      // Update Vuex state
      const updatedSketch = { ...this.sketch, name: this.newSketchName }
      this.$store.commit('SET_SKETCH', { 
        objects: [updatedSketch], 
        meta: this.$store.state.meta 
      })
      
      this.$emit('close')
    },
    closeDialog: function () {
      this.newSketchName = this.sketch.name
      this.$emit('close')
    },
  },
  created() {
    this.newSketchName = this.sketch.name
  },
}
</script>

